.Section_top_home{
  width: 100%;
  height: 60%;
  overflow: hidden;
  position: absolute;
  display: flex;
  background-image: url(./images/_Banner\ 1\ \(1\).png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
  animation: change 15s infinite ease-in-out;
  z-index: 100;
}
.content_home{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;

}
.content_home h1{
  color: #fff;
  font-size: 60px;
  letter-spacing: 15px;
}
.content_home h1 span{
  color: #111;
}
.content_home a{
  background: #85c1ee;
  padding: 10px 24px;
  text-decoration: none;
  font-size: 18px;
  border-radius: 20px;
}
.content_home a:hover{
  background: #034e88;
  color: #fff;
}
@keyframes change{
  0%
  {
      background-image: url(./images/banner\ 2.png);
  }
  20%
  {
      background-image: url(./images//banner\ 3.png);
  }
  40%
  {
      background-image: url(./images/banner\ 4.png);
  }
  60%
  {
      background-image: url(./images/banner\ 5.png);
  }
  80%
  {
      background-image: url(./images/banner\ 2.png);
  }
  100%
  {
      background-image: url(./images/banner\ 3.png);
  }
}

@media screen and (max-width: 768px) {
  .Section_top_home {
    height:200px;
    width: 100%;
    margin-top: 2px;
    background-size: contain;
  }

  .content_home h1 {
    font-size: 36px;
    letter-spacing: 8px;
  }

  .content_home a {
    font-size: 16px;
  }
}
