*{
    margin:0;
    padding:0;
    box-sizing: border-box;
  }
  
  .main{
    display: flex;
    align-items: center;
    justify-content: center;    
    /* background-image: linear-gradient(purple,rgb(238, 48, 238)); */
    background-image: url("https://media.istockphoto.com/id/1454707439/photo/scattered-question-marks-blocks-and-copy-space.webp?b=1&s=170667a&w=0&k=20&c=yIq5os34TiPkP73f4D1KwRWC_r86-vYe0ikuaWHv_OU=");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
  }


.main-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    min-height: 450px;
    width: 500px;
    background-color: rgb(21,76,121);
    border-radius: 15px;
    /* box-shadow: 2px 2px 10px 1px rgb(102, 3, 102); */
    box-shadow: 2px 2px 10px 1px #333;  
    overflow: hidden;
    padding: 10px;
    position: relative;
    gap:30px;
  }

  .question{
    margin:8px;
    box-shadow: 0px 0px 10px 1px #000000;
    border-radius: 5px;
    background-color: white;
    padding:9px;
    font-size: medium;
    font-weight: 600;
    font-family: 'Times New Roman', Times, serif;
    min-width: 100%;
    min-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #302851;
  }

  #question-number{
    margin-right: 15px;
    padding: 6px;
    color: #302851;
  }

  .option-container{
    display: flex;
    flex-direction: column;
    margin:6px 4px;    
    width: 100%;
    padding: 10px;
  }
 
  .option-btn{
    box-shadow: 0px 0px 4px 2px rgb(0, 0, 0);
    padding: 5px;
    border-radius: 3px;
    border:none;
    outline: none;
    transition: 0.3s;
    margin: 5px;
    min-height: 30px;
    background-color: var(orange);
  }
  
  .checked{
    background-color: rgb(179, 83, 179);
    color:white;
    box-shadow: 3px 3px 4px 1px rgb(78, 69, 78);
  }
  .option-btn:hover{
    background-color: rgb(212, 118, 212);
    color:white;
  }
  #next-button{
    padding:10px;
    border:none;
    background-color: purple;
    background-color: #3da9f2;
    color:white;
    border-radius: 5px;
    box-shadow: 3px 3px 2px 1px rgb(0, 0, 0);
    font-size: larger;
    font-weight: bold;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    transition: 0.2s;
    width:100px;
    position: absolute;
    bottom: 15px;
  }
  #next-button:active{
    box-shadow:none;    
  }
  .show-score{
    position: absolute;
    color: white;
    top:30%;
    font-size: 40px;
    text-align: center;  
  }