/* Original Styles */
.category {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (minmax(16rem, 1fr))[auto-fit];
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    gap: 8.5rem;
    padding: 36rem 9% 24rem 9%;
  }
  
  .category .box {
    border-radius: 1rem;
    border: 1px solid black;
    box-shadow: 0px 0px 10px black;
    background: #f5f5f5;
    padding: 4rem 3rem;
    text-align: center;
    width: 25em;
    height: 25em;
    transition: transform 0.4s;
    gap: 1rem;
    margin-top: 50px;
  }
  
  .category .box:hover {
    transform: scale(1.1);
  }
  
  .category .box img {
    height: 11rem;
    margin-bottom: 4rem;
  }
  
  .category .box h3 {
    font-size: 1.8rem;
    color: #302851;
  }
  
  .image-container {
    position: relative;
    width: 300px;
    height: 300px;
  }
  
  .bottom-image {
    width: 5%;
    height: 9%;
    position: fixed;
    border-radius: 50%;
    bottom: 50px;
    right: 50px;
  }
  
  .bottom-image:hover {
    animation: bounce 1s;
  }
  
  h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: xx-large;
    height: 90px;
  }
  
  /* Additional Styles */
  .category .box {
    border-radius: 1rem;
    background: light-bg; /* Assuming 'light-bg' is a valid background value */
    padding: 4rem 3rem;
    text-align: center;
  
    img {
      height: 7rem;
      margin-bottom: 2rem;
    }
  
    h3 {
      font-size: 1.7rem;
      color: black;
    }
  }
  
  @media screen and (max-width: 768px) {
    .category-box{
      top: auto;
      margin-top: 20px;
    }
    .category{
        padding: 12rem 9% 0 9%;
        gap: 1rem;
    }
  }