/* Live.css */

body {
  /* margin: 0; */
  /* font-family: 'Arial', sans-serif; */
  background-color: lightblue;
  /* color: #ecf0f1; */
}

.mobile-name-prompt {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.cross_img{
width: 4rem;
margin-left: 34rem;
}

.mobile-name-prompt .modal {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.mobile-name-prompt h2 {
  margin-bottom: 20px;
}

.mobile-name-prompt input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}


.chat__section {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 600px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  /* background-color: #34495e; */
  background-image: url('../../Components/images/live_bg2.jpg');
  border-radius: 10px;
  overflow: hidden;
}

.brand {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  margin-top: -35px;
}

.brand img {
  margin-right: 10px;
}

.brand h1 {
  font-size: 24px;
  margin: 0;
  color: #ecf0f1;
}

.message__area {
  flex: 1;
  overflow-y: auto;
  border: 1px solid #2c3e50;
  border-radius: 10px;
  padding: 10px;
  margin-top: -4rem;
  margin-bottom: 0px;
  /* background-color: #2c3e50; */
  background-image: url('../../Components/images/live_bg.jpg');
}

.message {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  animation: fadeIn 0.5s ease-out, slideIn 0.5s ease-out;
}

.message h4 {
  margin: 0 0 5px;
  font-weight: bold;
  color: #ecf0f1;
}

.message.incoming {
  background-color: #3498db;
  color: #ecf0f1;
  align-self: flex-start;
  width: 28rem;
}

.message.outgoing {
  background-color: #2ecc71;
  color: #ecf0f1;
  align-self: flex-end;
  margin-left: auto;
  width: 28rem;
}

.sendbutton {
  position: absolute;
  width: 4rem;
  right: 0;
  margin: 1rem;
}

.input-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-container {
  display: flex;
  position: relative;
}

textarea {
  /* width: calc(100% - 20px); */
width: 55rem;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #2c3e50;
  border-radius: 10px;
  resize: none;
  margin-top: 10px;
  font-size: 16px;
  margin-bottom: -10px;
  color: #2c3e50;
  background-color: #ecf0f1;
  /* flex: 1; */
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(10px);
  }
  to {
    transform: translateY(0);
  }
}

@media screen and (max-width: 870px)  {
  textarea {
    /* width: calc(100% - 20px); */
  width: 30rem;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #2c3e50;
    border-radius: 10px;
    resize: none;
    margin-top: 10px;
    font-size: 16px;
    margin-bottom: -10px;
    color: #2c3e50;
    background-color: #ecf0f1;
    /* flex: 1; */
  }
  .message__area {
    flex: 1;
    overflow-y: auto;
    border: 1px solid #2c3e50;
    border-radius: 10px;
    width: 28rem;
    padding: 10px;
    margin-top: -4rem;
    margin-bottom: 0px;
    /* background-color: #2c3e50; */
    background-image: url('../../Components/images/live_bg.jpg');
  }
  .message.outgoing {
    background-color: #2ecc71;
    color: #ecf0f1;
    align-self: flex-end;
    margin-left: auto;
    width: 20rem;
    margin-left: 6rem;
  }
  .message.incoming {
    background-color: #3498db;
    color: #ecf0f1;
    align-self: flex-start;
    width: 20rem;
    margin-right: 6rem;
  }
}