  .container-Review {
    position: relative;
    width: 320px;
    margin: 100px auto 0 auto;
    perspective: 1000px;
    margin-bottom: 60rem;
    /* margin-top: 20%; */

  }
  .h1-tag {
    font-size: 4rem;
    font-weight: bold;
    margin-bottom: 48rem;
    /* color: 212121; */
    color: #224bcf;
    /* text-shadow: 2px 2px black; */
    margin-top: -20rem;
}
  
  .carousel_1 {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d; 
    animation: rotate360 60s infinite forwards linear;
   
  }
  .carousel_face { 
    position: absolute;
    width: 300px;
    height: 300px;
    top: -238px;
    left: 10px;
    right: 10px;
    font-size:smaller;
    background-size: cover;
    color: #224bcf;
    display: flex;
    box-shadow: 0px 0px 20px black;
    cursor: pointer;
  }
  
  span {
    margin: auto;
    font-size: 2rem;
  }
  
  
  .carousel_face:nth-child(1) {
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ FIRST\ .png");
    transform: rotateY(  0deg) translateZ(430px); }
  .carousel_face:nth-child(2) { 
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ SECOND.png");
      transform: rotateY( 40deg) translateZ(430px); }
  .carousel_face:nth-child(3) {
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ THIRD.png");
    transform: rotateY( 80deg) translateZ(430px); }
  .carousel_face:nth-child(4) {
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ FOURTH.png");
    transform: rotateY(120deg) translateZ(430px); }
  .carousel_face:nth-child(5) { 
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ FIFTH.png");
   transform: rotateY(160deg) translateZ(430px); }
  .carousel_face:nth-child(6) { 
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ SIX.png");
   transform: rotateY(200deg) translateZ(430px); }
  .carousel_face:nth-child(7) { 
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ SEVEN.png");
   transform: rotateY(240deg) translateZ(430px); }
  .carousel_face:nth-child(8) {
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ EIGHT.png");
    transform: rotateY(280deg) translateZ(430px); }
  .carousel_face:nth-child(9) {
    background-color:#f5f5f5;
    background-image: url("/public/images/review\ NINE.png");
    transform: rotateY(320deg) translateZ(430px); }
  
  
  
  @keyframes rotate360 {
    from {
      transform: rotateY(0deg);
    }
    to {
      transform: rotateY(-360deg);
    }
  }

  @media screen and (max-width: 768px) {
    .container-Review {
      width: 100%;
      margin: 50px auto 0 auto;
    }
  
    .carousel1_face {
      width: 100%;
      max-width: 300px;
      height: 200px; /* Adjust as needed */
      top: -150px; /* Adjust as needed */
      left: 5px; /* Adjust as needed */
      right: 5px; /* Adjust as needed */
    }
  
    span {
      font-size: 1.5rem; /* Adjust as needed */
    }
  }
  