
.header {
  position: -webkit-sticky;
  position: sticky;
  /* max-height: 100px; */
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 9%;
  background: rgba(0, 0, 255, 0.7);
}

.header .logo {
  font-size: 2.5rem;
  font-weight: bolder;
  color: #fff;
  margin-left: -18rem;
}

.header .logo i {
  color: rgba(250, 29, 136, 0.5);
}

.img_logo{
  width: 4.5rem;
  border-radius: 50px;
  margin-left: -8rem;
}

.header .navbar ul {
  list-style: none;
}

.header ul {
  list-style: none;
}

.header .navbar ul li {
  position: relative;
  float: left;
}

.header .navbar ul li:hover ul {
  display: block;
}

.header .navbar ul li a {
  padding: 2rem;
  display: block;
  font-size: 1.7rem;
  color: #fff;
}

.header .navbar ul li a:hover {
  background: rgba(250, 29, 136, 0.5);
  transition: all ease 0.5s;
}

.header .navbar ul li ul {
  position: absolute;
  left: 0;
  width: 20rem;
  background: rgba(0, 0, 255, 0.5);
  display: none;
}

.header .navbar ul li ul li {
  width: 100%;
}

/* .menu-btn {
  cursor: pointer;
  color: #fff;
  font-size: 2.5rem;
  display: none;
} */

.menu-btn {
  cursor: pointer;
  color: #fff;
  zoom: 200%;
  display: none;
}


/* ----------------------------- */


.header{
  position:fixed;
  top:0; left:0; right:0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:0 9%;
  background: rgba(0, 0, 255, 0.5);

  .logo{
      font-size: 2.5rem;
      font-weight: bolder;
      color:white;

      i{
          color:pink;
      }
  }
  .navbar{

      ul{
          list-style: none;

          li{
              position: relative;
              float: left;

              &:hover ul{
                  display: block;
              }

              a{
                  padding:2rem;
                  display: block;
                  font-size: 1.7rem;
                  color:white;

                  &:hover{
                      background: pink;
                  }
              }

              ul{
                  position: absolute;
                  left:0;
                  width:20rem;
                  background: rgba(0, 0, 255, 0.5);
                  display: none;

                  li{
                      width: 100%;
                  }
              }
          }
      }
  }
}

/* Existing styles above this line remain unchanged */

@media screen and (max-width: 1114px) {
  /* .menu-btn {
    display: inline-block;
  } */

  .header {
    padding: 2rem;
    max-height: 100px;
  }

  .header .navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    background: rgba(0, 0, 255, 0.2);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }

  .header .navbar.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }

  .header .navbar ul li {
    width: 100%;
  }

  .header .navbar ul li ul {
    width: 100%;
    position: relative;
  }

  .about .content h3 {
    font-size: 3rem;
  }

  .main-video video {
    width: 95%;
    border-width: 0.2rem;
  }
}

@media screen and (max-width: 768px) {
  .menu-btn {
    display: block;
  }
  .img_logo{
    width: 4.5rem;
    border-radius: 50px;
    margin-left: -1rem;

  }
  .header .logo{
    margin-left: 0rem;
  }

  /* .menu-btn{
      display: inline-block;
  } */

  .header {
    padding: 2rem;
    height: 65px;
  }

  .header .navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    background: rgba(0, 0, 255, 0.5);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);

    &.active {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    ul li {
      width: 100%;

      ul {
        width: 100%;
        position: relative;
      }
    }
  }

  .about .content h3 {
    font-size: 3rem;
  }

  .main-video video {
    width: 95%;
    border-width: 0.2rem;
  }
}


