
.heading {
    /* background: url({./images/heading-bg.png} )no-repeat; */
    background-size: cover;
    background-position: center;
  }
  
  .heading h3 {
    font-size: 3.5rem;
    color: #302851;
  }
  
  .heading p {
    font-size: 1.6rem;
    color: #666;
    padding-top: .5rem;
  }
  
  .heading p a {
    padding-right: .5rem;
    color: #fa1d86;
  }
  
  .heading p a:hover {
    color: #224bcf;
  }
  .course-1 {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (minmax(31rem, 1fr))[auto-fit];
        grid-template-columns: repeat(auto-fit, minmax(31rem, 1fr));
    gap: 1.5rem;
  }
  
  .course-1 .box {
    background: #f5f5f5;
    border-radius: 1rem;
    text-align: center;
    padding: 3rem 2rem;
  }
  
  .course-1 .box img {
    margin-bottom: 1rem;
    height: 8rem;
  }
  
  .course-1 .box h3 {
    font-size: 2rem;
    color: #302851;
    padding: .5rem 0;
  }
  
  .course-1 .box p {
    font-size: 1.4rem;
    color: #666;
    line-height: 2.5;
    padding: 1rem 0;
  }
  .image-container {
    position: relative;   
    width: 300px;     
    height: 300px;    
  } 
   .bottom-image {
    width: 5%;
    height: 9%;
    position: fixed;
    border-radius: 50%;
    bottom: 50px;
    right: 50px;
  }
  .bottom-image:hover{
    animation: bounce 1s;
  } 
  @keyframes bounce {
    0%, 20%, 60%, 100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
  
    40% {
      -webkit-transform: translateY(-20px);
      transform: translateY(-20px);
    }
  
    80% {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
    }
  }
    
  