.body {
    font-family: 'Lato', sans-serif;
}
.img_rw{
  width: 5rem;
  margin-left: 120rem;
}

.containerReview {
    text-align: center;
    padding: 20px;
    background: linear-gradient(to top left ,#3ed8ff, #a8f5ff);
  }
  
  h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    color: 212121;
    text-shadow: 2px 2px black;
    margin-top: -9rem;
  }
  
  .carousel {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .carousel__face {
    width: 300px;
    padding: 20px;
    color:black;
    margin: 10px;
    background: linear-gradient(to top left, #ffffffff, #ffffff33);
    backdrop-filter: blur(20px);
    z-index:2;
    border-radius: 8px;
    box-shadow: 3px 3px 5px #0a748f;
  }
  
  .add-review-form {
    color: 212121;
    margin-top: 30px;
    padding: 20px;
    background: linear-gradient(to top left, #ffffffff, #ffffff33);
    border-radius: 8px;
    z-index: 3;
    box-shadow: 3px 3px 5px #0a748f;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    background-color: white;
    border-radius: 8px;
    color:black;
  }
  
  button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }

  .circle{
    background: linear-gradient(to top right ,#ffffffff, #ffffff33);
    width: 200px;
    height: 200px;
    border-radius: 100px;;
    position: absolute;
}

.c1{
    top: 60px;
    left: 130px;
}

.c2{
    right: 110px;
    bottom: 80px
}

.c3{
    top: 60px;
    right: 30px;
}

.c4{
    left: 30px;
    bottom: 60px
}
@media screen and (max-width: 768px) {
  .rw_txt {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    background-color: white;
    border-radius: 8px;
    color:black;
  }
  .img_rw{
    width: 5rem;
    margin-left: 23rem;
  }
 }