.footer {
    background: #f5f5f5;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
  }
  
  .footer .box-container1 {
    display: -ms-grid;
    display: flex;
    -ms-grid-columns: (minmax(25rem, 1fr))[auto-fit];
        grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    gap: 6rem;
    
  }

  .box-container2 img{
    width:350px;
    height: 300px;
    border-radius: 50%;
  }
  
  .footer .box-container1 .box h3 {
    font-size: 2.2rem;
    padding: 1rem 0;
    color: #302851;
  }
  
  .footer .box-container1 .box a {
    display: block;
    font-size: 1.4rem;
    padding: 1rem 0;
    color: #666;
  }
  
  .footer .box-container1 .box a:hover {
    color: #fa1d86;
  }
  
  .footer .box-container1 .box a:hover i {
    padding-right: 2rem;
  }
  
  .footer .box-container1 .box a i {
    color: #fa1d86;
    padding-right: .5rem;
  }
  
 .credit {
    text-align: center;
    margin-top: 2rem;
    padding: 1rem;
    padding-top: 2.5rem;
    font-size: 2rem;
    color: #666;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
  }
  
 .credit span {
    color: #fa1d86;
  }
