.heading img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.heading h3 {
  font-size: 3.5rem;
  color: #765827;
}

.heading p {
  font-size: 1.6rem;
  color: #C8AE7D;
  padding-top: 5rem;
}

.heading p a {
  padding-right: 5rem;
  color: #fa1d86;
}

.heading p a:hover {
  color: #862B0D;
}
.about {
  /* display: -webkit-box;
  display: -ms-flexbox; */
  display: flex;
  /* -webkit-box-align: center;
      -ms-flex-align: center; */
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 2rem;
  width:100%
}

.about .image {
  width:50%
}

.about .image img {
  width: 75%;
  display: inline;
}

.about .content {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 42rem;
          flex: 1 1 42rem;
}

.about .content h3 {
  color: #302851;
  font-size: 4.5rem;
}

.about .content p {
  font-size: 1.4rem;
  padding: 1rem 0;
  color: #C8AE7D;
  line-height: 2.5;
}

.content .btn {
  margin-top: 20px;
  background-color: #3da9f2;
  padding: 10px;
}
.content .btn:hover {
  background-color: #224bcf;
}

.photo-container{
  position: relative;
  margin: 100px auto 0 auto;
  perspective: 1000px;
  margin-bottom: 10%;
  display: flex;
  
}

.pic{
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  border: 1px solid black;
  box-shadow: 0px 0px 10px black;
  background: #f5f5f5;
  padding: 4rem 3rem;
  text-align: center;
  width: 25em;
  height: 27em;
  transition: transform .4s;
  gap: 15px;
  margin: 10px;
}

.pic:hover{
  transform: scale(1.1) ;
}

.pic img{
  width: 19em;
  height: 19em;
  margin: -5px;
}

.pic h3{
  font-size: large;
  font-weight: 700;
}

@media screen and (max-width: 768px){
  .photo-container{
    position: relative;
    margin: 100px auto 0 auto;
    perspective: 1000px;
    margin-bottom: 10%;
    display: flex;
    flex-direction: column;
  }
}